.todo__form p {
    color: rgb(194, 50, 50);
}

.form__title {
    margin-top: 20px;
    font-size: 35px;
}

.form__item {
    margin: 20px;
}

label {
    margin-right: 20px;
    font-size: 22px;
    font-weight: bold;
    letter-spacing: 1px;
}

input {
    font-size: 22px;
}

.form__submit {
    width: 160px;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    margin: 20px;
    height: 55px;
    text-align: center;
    border: none;
    background-size: 300% 100%;
    border-radius: 50px;
    transition: all .4s ease-in-out;
    background-image: linear-gradient(to right,
            #eb3941,
            #f15e64,
            #e14e53,
            #e2373f);
    box-shadow: 0 5px 15px rgba(242, 97, 103, 0.4);
}

.form__submit:hover {
    background-position: 100% 0;
    transition: all .4s ease-in-out;
}

.form__submit:focus {
    outline: none;
}