}

.todo__form p {
    color: rgb(194, 50, 50);
}

.form__title {
    margin-top: 20px;
    font-size: 35px;
}

.form__item {
    margin: 20px;
}

label {
    margin-right: 20px;
    font-size: 22px;
    font-weight: bold;
    letter-spacing: 1px;
}

input {
    font-size: 22px;
}

.form__submit {
    width: 160px;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    margin: 20px;
    height: 55px;
    text-align: center;
    border: none;
    background-size: 300% 100%;
    border-radius: 50px;
    transition: all .4s ease-in-out;
    background-image: linear-gradient(to right,
            #eb3941,
            #f15e64,
            #e14e53,
            #e2373f);
    box-shadow: 0 5px 15px rgba(242, 97, 103, 0.4);
}

.form__submit:hover {
    background-position: 100% 0;
    transition: all .4s ease-in-out;
}

.form__submit:focus {
    outline: none;
}
.task {
    background-color: #2B2B2B;
    border-bottom: 5px solid #38EF7D;
    padding: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
    max-width: 900px;
    margin: 20px auto;
    font-size: 20px;
}

@media(min-width: 820px) {
    .task {
        flex-direction: row;
    }

}

.task__title {
    max-width: 35%;
    word-wrap: break-word;
    margin: 10px;
}

.task__text {
    max-width: 40%;
    word-wrap: break-word;
    margin: 10px;

}

.task__buttons button {
    cursor: pointer;
    margin: 0 5px;
    padding: 5px 10px;
    border: none;
    background: none;
    background-color: white;
    border-radius: 4px;
    font-size: 17px;
}

.task__buttons .task__delete {
    border: 2px solid #E23F46;
}

.task__buttons .task__done {
    border: 2px solid #38EF7D;
}

.task__done:hover {
    background-color: #38EF7D
}

.task__delete:hover {
    background-color: #E23F46
}
.numberOfTasks {
    padding: 20px;
    background-color: #1F1F1F;
    max-width: 120px;
    margin: 20px auto;
    text-align: center;
    border-radius: 30px;
    font-size: 22px;
}
